 .product-card {
    width: 290px;
    height: 540px;
    padding: 10px;
    border: 1px solid #eaeaea;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    transition: box-shadow 0.3s ease;
  }

  .product-card:hover {
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
  }

  .product-image {
    position: relative;
    text-align: center;
  }

  .product-image img {
    max-width: 100%;
    border-radius: 10px;
  }

  .wishlist-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
  }

  .wishlist-btn:hover {
    color: #ff6f61;
  }

  .product-info {
    margin-top: 10px;
  }

  .product-name {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 5px;
  }

  .product-description {
    font-size: 14px;
    color: #777;
    margin-bottom: 10px;
  }

  .product-price {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 15px;
  }

  .add-to-bag-btn {
    padding: 10px;
    width: 100%;
    background-color: #333;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }

  .add-to-bag-btn:hover {
    background-color: #ff6f61;
  }